import React, { useState, useEffect } from 'react';
import { Input, Button, Timeline, Modal, Card } from 'antd';
import axios from 'axios';
import './App.css'; //  CSS 

const { TextArea } = Input;
const App = () => {
  const [historyRecords, setHistoryRecords] = useState([]);
  const [historyDivFormat, sethistoryDivFormat] = useState(new Map());

  const [year, setYear] = useState('');
  const [name, setName] = useState('');

  const [cause, setCause] = useState('');
  const [event, setEvent] = useState('');
  const [influnce, setInflunce] = useState('');
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [modalTitle, setModalTitle] = useState('')
  const [eventId, setEventID] = useState();

  const showModal = () => {
    setModalTitle('');
    setYear('');
    setName('');
    setCause('');
    setEvent('');
    setInflunce('');
    setIsModalVisible(true);
  };
  const showModifyModal = (history) => {
    setModalTitle('');
    setEventID(history.EventID)
    setYear(history.EventYear);
    setName(history.EventName);
    setCause(history.EventCause);
    setEvent(history.Event);
    setInflunce(history.EventInfluence);
    setIsModalVisible(true);
  }

  const handleCancel = () => {
    setIsModalVisible(false);
  };


  function getCurrent() {
    // GET
    axios.get('http://3.23.99.16:4000/history_records', {
      headers: {
        'Access-Control-Allow-Origin': '*' // 
      }
    }).then(response => {
      // state
      var array = response.data;
      const sorted = [...array].sort((a, b) => compareDates(a.EventYear, b.EventYear));
      setHistoryRecords(sorted);
    })
      .catch(error => {
        // 
        console.error('Error fetching data:', error);
      });
  }

  useEffect(() => {
    console.log('useEffect getCurrent');
    getCurrent();
  }, []); // 

  useEffect(() => {
    // 
    console.log('useEffect historyRecords');
    const newObjectMap = new Map();
    historyRecords.forEach(obj => {
      newObjectMap.set(obj.EventID, obj);
    });

    // 
    sethistoryDivFormat(newObjectMap);
  }, [historyRecords]);

  function compareDates(dateA, dateB) {
    const [yearA, monthA, dayA] = parseDate(dateA);
    const [yearB, monthB, dayB] = parseDate(dateB);

    if (yearA !== yearB) {
      return yearA - yearB;
    } else if (monthA !== monthB) {
      return monthA - monthB;
    } else {
      return dayA - dayB;
    }
  }

  function parseDate(dateString) {
    const parts = dateString.split('.');
    const year = parseInt(parts[0]);
    const month = parts[1] ? parseInt(parts[1]) : 1; // Default to January if month is not provided
    const day = parts[2] ? parseInt(parts[2]) : 1;   // Default to 1st day if day is not provided
    return [year, month, day];
  }

  // function formatDate(dateString) {
  //   const date = new Date(dateString);
  //   const month = date.getMonth() + 1;
  //   const day = date.getDate();
  //   return `${month}-${day}`;
  // }


  function addHistory() {
    let postData = {
      id: 1,
      name: name,
      year: year,
      cause: cause,
      event: event,
      influence: influnce
    };
    axios.post('http://3.23.99.16:4000/history_records', postData, {
      headers: {
        'Access-Control-Allow-Origin': '*' // 
      }
    }).then(response => {
      // state
      setIsModalVisible(false);
      getCurrent();
    })
      .catch(error => {
        // 
        getCurrent();
        console.error('Error fetching data:', error);
      });
  }

  function modifyHistory() {
    console.log(eventId);
    let postData = {
      eventid:  eventId,
      id: 1,
      name: name,
      year: year,
      cause: cause,
      event: event,
      influence: influnce
    };
    axios.put('http://3.23.99.16:4000/history_records', postData, {
      headers: {
        'Access-Control-Allow-Origin': '*' // 
      }
    }).then(response => {
      setIsModalVisible(false);
      // state
      getCurrent();
    })
      .catch(error => {
        // 
        getCurrent();
        console.error('Error fetching data:', error);
      });
  }

  const deleteHistory = () => {
    axios.delete('http://3.23.99.16:4000/history_records', {
      data: { id: eventId }, //  id 
      headers: {
        'Access-Control-Allow-Origin': '*' // 
      }
    }).then(response => {
      // 
      setIsModalVisible(false);
      getCurrent();
      // 
    }).catch(error => {
      // 
      console.error('Error deleting record:', error);
    });
  };

  const historys = Array.from(historyDivFormat.values()).map(history => {
    const children = (
      <Card
        className="history-card"
        onClick={() => showModifyModal(history)}
        hoverable
      >
        <div>{history.EventName}</div>
        <div>: {history.EventYear}</div>
        <div>: {history.EventCause}</div>
        <div>: {history.Event}</div>
        <div>: {history.EventInfluence}</div>
      </Card>
    );

    return { children };
  });


  return (
    <>
      <div>
        <Button type="primary" onClick={showModal}>
          
        </Button>
        <Modal
          title={modalTitle}
          open={isModalVisible}
          onCancel={handleCancel}
          footer={null} // 
        >
          <div>
            <div>
              : <input value={year} onChange={(e) => setYear(e.target.value)} placeholder=""></input>
              : <input value={name} onChange={(e) => setName(e.target.value)} placeholder=" "></input>

              <div
                style={{
                  margin: '24px 0',
                }}
              />
              :<TextArea value={cause} onChange={(e) => setCause(e.target.value)} placeholder="" autoSize />
              :<TextArea value={event} onChange={(e) => setEvent(e.target.value)} placeholder="" autoSize />
              :<TextArea value={influnce} onChange={(e) => setInflunce(e.target.value)} placeholder="" autoSize />
            </div>
            <div
              style={{
                margin: '24px 0',
              }}
            />
            {modalTitle===''&&(
              <Button type="primary" onClick={addHistory}>
                
              </Button>
            )}
            {modalTitle===''&&(
              <div>
                <Button type="primary" onClick={modifyHistory}>
                  
                </Button>
                <Button type="primary" onClick={deleteHistory} danger>
                  
                </Button>
              </div>
            )}
          </div>
        </Modal>
      </div>
      { historys.length < 10 && (
        <Timeline
          className='one-time-line'
          items={historys}
        />
      )}
      { historys.length >= 10 && (
        <div className='two-time-lines'>
          <Timeline
            className='time-line'
            items={historys.slice(0, Math.ceil(historys.length / 2))}
          />
          <Timeline
            className='time-line'
            items={historys.slice(Math.ceil(historys.length / 2))}
          />
        </div>
      )}
    </>
  );
};
export default App;